body{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(238, 69, 64);
    background: linear-gradient(
        180deg,
        rgba(238, 69, 64, 1) 0%,
        rgba(199, 44, 65, 1) 15%,
        rgba(128, 19, 54, 1) 30%,
        rgba(81, 10, 50, 1) 45%,
        rgba(45, 20, 44, 1) 100%
    );  
    background-size: cover;
    background-attachment: fixed;
}

.container{
    position: relative;
    width: 100%;
    height: 100vh;
}

@media screen and (max-width: 767px){
    .container{
        height: 100%;
    }
    .container .card{
        margin: 20px 0;
    }
}